.player-container {
  position: absolute;
}
.info {
  font-size: 80%;
}
.info-child {
  font-size: 60%;
}
.soccerplayer-background {
  display: flex;
  flex-direction: column;
  width: 36px;
  height: 36px;
  border-radius: 100%;
  border-width: 20px;
  border: solid;
  color: #ffffff;
  border-color: #717485;
  background-color: rgb(51, 54, 71);
  text-align: center;
  align-items: center;
  justify-content: center;
  position: absolute;
}
